export default {
	name: 'Image',
	props: ['src', 'alt'],
	data() {
		return {
			is_loading: true,
			is_broken: false
		}
	},
	created() {
		this.handle_image();
	},
	methods: {
		handle_image() {
			let img = new Image();
			img.src = this.src;

			img.onload = () => {
				this.is_loading = false;
			};
			img.onerror = () => {
				this.is_loading = false;
				this.is_broken = true;
			};
		}
	}
};
